.navbar {
    width: 100%;
    height: 5.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #ffffff;
    font-family: Bw Quinta Pro;
    box-shadow: 0px 3px 9px rgba(0, 0, 0, 0.05);
    color: #222;
  }
  
  .navbar-logo {
    display: flex;
    align-items: center;
  }
  .nav-menu {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }
  
  .nav-links {
    color: rgb(22, 21, 21);
    text-decoration: none;
    padding: 0.5rem 1rem;
  }
  
  .nav-links li a {
    margin: 0 1rem;
    cursor: pointer;
    text-decoration: none;
    color: #000;
    }
    .nav-links a:hover {
    color: rgb(65, 61, 61);
    }
  .nav-links-mobile {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-left: 60px;
    color: var(--color-green);
  }
  .app_navbar-login{
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .app_navbar-login div{
    width: 1px;
    height: 30px;
    background-color: var(--color-green);
}
.app_navbar-login a{
    margin: 0 1rem;
    text-decoration: none;
    transition: 0.5s ease;
    color: var(--color-green);
}
.app_navbar-login a:hover{
    border-bottom: 1px solid var(--color-green);
}
  .nav-links-mobile:hover{
    border-bottom: var(--color-green);
  }
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 67px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #fafafa;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 1rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
        color: rgb(65, 61, 61);
        }
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      -webkit-transform: translate(25%, 50%);
              transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-transform: translate(-100%, 60%);
              transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
    .nav-icon{
        display: flex;
        align-items: center;
 
    }
    .app_navbar-login {
     margin-bottom: 1rem;
    }
  
    .nav-links-mobile:hover{
        background-color: var(--color-green);
        color: #fff;
      transition: 250ms;
    }
  }
  .dropdown-menu {
    width: 200px;
    position: absolute;
    top: 67px;
    list-style: none;
    text-align: start;
    background: rgb(187, 192, 192);
  }
  
  .dropdown-menu li {   
    list-style: none; 
    cursor: pointer; 
    color: rgb(239, 245, 245);
    border-radius: 10px;
  }
  
  .dropdown-link li:hover {
      background-color: #ffffff;

  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: #222;
    padding: 16px;
  }
  
